import React, { FunctionComponent } from "react";
import styled from "styled-components";
import { AnimateCSS, IAnimateCSS } from "../AnimateCSS"

const ShakeStyled = styled(AnimateCSS)`

  @keyframes shake {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translate3d(-10px, 0, 0);
    }

    20%,
    40%,
    60%,
    80% {
      transform: translate3d(10px, 0, 0);
    }
  }


  animation-name: ${p => p.animate ? "shake" : "none"};
`;

const Shake: FunctionComponent<IAnimateCSS> = props => {

  const { children, ...others } = props;

  return (
    <ShakeStyled {...others}>
      {children}
    </ShakeStyled>
  );
}

export default Shake;
try {
    // @ts-ignore
    Shake.displayName = "Shake";
    // @ts-ignore
    Shake.__docgenInfo = { "description": "", "displayName": "Shake", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/Shake/index.tsx#Shake"] = { docgenInfo: Shake.__docgenInfo, name: "Shake", path: "src/components/Shake/index.tsx#Shake" };
}
catch (__react_docgen_typescript_loader_error) { }